import React from "react";
import { Link } from "react-router-dom";

const Home = () => {
  return (
    <div className="section home">
      <article className="home-text">
        <h2>Hi, I´m Isaac.</h2>
        <p>I'm a front-end developer who enjoys creating user interfaces.</p>
        <Link to="/Portfolio/about">
          <button className="home-button">Click to see more about me</button>
        </Link>
      </article>
    </div>
  );
};

export default Home;
