import React from "react";
import ContactCard from "./ContactCard";
import instagram from "../images/instagram.png";
import linkedin from "../images/linkedin.png";
import whatsapp from "../images/whatsapp.png";

const Contact = () => {
  return (
    <div className="section contact">
      <div className="contact-container">
        <a href="https://www.instagram.com/_isaac.js/?hl=es-la">
          <ContactCard name={"Instagram"} image={instagram} />
        </a>
        <a href="https://www.linkedin.com/in/isaac-salazar-sanchez-21194b1ba/">
          <ContactCard name={"LinkedIn"} image={linkedin} />
        </a>
        <a href="https://wa.me/573107463381">
          <ContactCard name={"Whatsapp"} image={whatsapp} />
        </a>
      </div>
    </div>
  );
};

export default Contact;
