import React, { useState } from "react";
import Cards from "./Cards";

import htmlLogo from "../images/html5-logo.png";

import cssLogo from "../images/css3-logo.png";

import jsLogo from "../images/javascript-logo.png";

import reactLogo from "../images/react-logo.png";

import reduxLogo from "../images/redux-logo.png";

import nodeLogo from "../images/node-logo.png";

import hooksLogo from "../images/hooks-logo.png";

import gitLogo from "../images/git-logo.png";

import gitHubLogo from "../images/github-logo.png";

import visualStudioLogo from "../images/visual-studio-code-logo.png";

import computerImg from "../images/computer.png";
import { Link } from "react-router-dom";

const cardsData = [
  {
    id: 0,
    logo: htmlLogo,
    name: "HTML",
  },
  {
    id: 1,
    logo: cssLogo,
    name: "CSS",
  },
  {
    id: 2,
    logo: jsLogo,
    name: "JAVASCRIPT",
  },
  {
    id: 3,
    logo: reactLogo,
    name: "REACT",
  },
  {
    id: 4,
    logo: hooksLogo,
    name: "HOOKS",
  },
  {
    id: 5,
    logo: reduxLogo,
    name: "REDUX",
  },
  {
    id: 6,
    logo: nodeLogo,
    name: "NODEJS",
  },
  {
    id: 7,
    logo: gitLogo,
    name: "GIT",
  },
  {
    id: 8,
    logo: gitHubLogo,
    name: "GITHUB",
  },
  {
    id: 9,
    logo: visualStudioLogo,
    name: "VISUAL STUDIO CODE",
  },
];

const About = () => {
  const [tecData] = useState(cardsData);

  return (
    <div className="section about">
      <section className="introduction-container">
        <article className="introduction">
          <h2 className="introduction-title">Hi, nice to meet you.</h2>
          <p className="introduction-text">
            I am a front-end developer experienced in using react. I like to
            code things from scratch, and enjoy bringing ideas to life in the
            browser.
          </p>
          <img src={computerImg} alt="computer" className="about-img" />
          <section className="cards-container">
            <h2 className="cards-container-title">
              To create friendly user interfaces I use various technologies and
              languages ​​such as:
            </h2>
            {tecData.map((item) => (
              <Cards key={item.id} tec={item} />
            ))}
          </section>
          <section className="portfolio-link-button">
            <h2 className="portfolio-link-title">
              Click on the following button if you want to see a little bit of
              my work.
            </h2>
            <Link to="/Portfolio/projects">
              <button>Let's see it!</button>
            </Link>
          </section>
        </article>
      </section>
    </div>
  );
};

export default About;
